/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Happy Birthday Grandma Shirley","date":"17 Jan 2000","slug":"/2000-01-17-happy-birthday-grandma-shirley/","tags":"Y2000"},"html":"<p>We just called Grandma at home in Minnesota to wish her a happy birthday, but I thought we'd repeat the wish here for all to see! </p>\n<p>Our cats have been complaining that Christopher has been getting too much face time on this website. In order to keep peace in the household, here they are. We've had all three for over 10 years now, since we were in Minneapolis.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/mausi0117.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"mausi0117.jpg: \" /><br />\nThis is Mausi, the oldest and smallest. He's too sophisticated to have anything to do with the baby or the other cats. Here he's alone in our bedroom when everyone else is downstairs.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/helga0117.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"helga0117.jpg: \" /><br />\nThis is Helga, our streetwise tabby cat. She likes adults, but stays away from children. She's taken the longest to acknowledge the baby, although she's gotten pretty close here. (And, yes, that's a stuffed version of the Maus Newsletter mascot behind her.)</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/karlheinz0117.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"karlheinz0117.jpg: \" /><br />\nThis is Karl-Heinz, Mama's favorite. He's accepted Christopher, if only to stay close to her. This is a typical pose, sleeping obscenely on his back.</p>"},"next":{"fields":{"title":"Up too late","date":"16 Jan 2000","slug":"/2000-01-16-up-too-late/"}},"previous":{"fields":{"title":"Kohl Quits, Won't Tell","date":"18 Jan 2000","slug":"/2000-01-18-kohl-quits-wont-tell/"}}},{"node":{"fields":{"title":"Up too late","date":"16 Jan 2000","slug":"/2000-01-16-up-too-late/","tags":"Y2000"},"html":"<p>I stayed up late to watch the Vikings play St. Louis. The DSF German sports channel shows NFL games at 11:30pm Sunday evenings. They are delayed by a couple of hours but most of the commerical breaks are cut. So it only took me 1&nbsp;1/4 hours of real time to give up on the Vikings (I went to bed at 42-17 in the 4th quarter).</p>"},"next":{"fields":{"title":"He looks like Mama too","date":"15 Jan 2000","slug":"/2000-01-15-he-looks-like-mama-too/"}},"previous":{"fields":{"title":"Happy Birthday Grandma Shirley","date":"17 Jan 2000","slug":"/2000-01-17-happy-birthday-grandma-shirley/"}}},{"node":{"fields":{"title":"He looks like Mama too","date":"15 Jan 2000","slug":"/2000-01-15-he-looks-like-mama-too/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/fphpunk.jpg\" height=\"390\" width=\"300\" border=\"0\" alt=\"fphpunk.jpg: \" /><br />\nMama has insisted on equal time. Here is her baby picture at age 10 days to show that Christopher has some resemblence to her as well. She also wants to point out that in June 1963 the punk hairstyle was quite futuristic. (More of her baby pics are <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=1529487\">here</a>.)</p>"},"next":{"fields":{"title":"Grandma Ruth","date":"14 Jan 2000","slug":"/2000-01-14-grandma-ruth/"}},"previous":{"fields":{"title":"Up too late","date":"16 Jan 2000","slug":"/2000-01-16-up-too-late/"}}}],"pathPrefix":"","first":false,"last":false,"index":1089,"pageCount":1101}}

/***/ })

